body {
  margin: 0;
  padding: 0;
  background-color: rgba(5, 71, 71, 0.398);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#movies img,
#movie img {
  width: 100%;
}
.brand-text {
  font-size: 2rem;
}

@media (min-width: 960px) {
  #movies .col-md-3 .well {
    height: 390px;
  }

  #movies .col-md-3 img {
    height: 240px;
  }
}


  #slideshow {
	/* margin: 0 auto; */
	padding-top: 1px;
	height: 400px;
	width: 100%;
	box-sizing: border-box;

}

.entire-content {
	margin: auto;
	width: 190px;
	-webkit-perspective: 1000px;
	        perspective: 1000px;
	position: relative;
	padding-top: 80px;
}

.content-carrousel {
	width: 100%;
	position: absolute;
	float: right;
	-webkit-animation: rotar 15s infinite linear;
	        animation: rotar 15s infinite linear;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}

.content-carrousel:hover {
	-webkit-animation-play-state: paused;
	        animation-play-state: paused;
	cursor: pointer;
}

.content-carrousel figure {
	width: 100%;
	height: 180px;
	border: 1px solid #3b444b;
	overflow: hidden;
	position: absolute;
}

.content-carrousel figure:nth-child(1) {
	-webkit-transform: rotateY(0deg) translateZ(300px);
	        transform: rotateY(0deg) translateZ(300px); 
} .content-carrousel figure:nth-child(2) {
	-webkit-transform: rotateY(40deg) translateZ(300px);
	        transform: rotateY(40deg) translateZ(300px); 
} .content-carrousel figure:nth-child(3) {
	-webkit-transform: rotateY(80deg) translateZ(300px);
	        transform: rotateY(80deg) translateZ(300px); 
} .content-carrousel figure:nth-child(4) {
	-webkit-transform: rotateY(120deg) translateZ(300px);
	        transform: rotateY(120deg) translateZ(300px); 
} .content-carrousel figure:nth-child(5) {
	-webkit-transform: rotateY(160deg) translateZ(300px);
	        transform: rotateY(160deg) translateZ(300px); 
} .content-carrousel figure:nth-child(6) {
	-webkit-transform: rotateY(200deg) translateZ(300px);
	        transform: rotateY(200deg) translateZ(300px); 
} .content-carrousel figure:nth-child(7) {
	-webkit-transform: rotateY(240deg) translateZ(300px);
	        transform: rotateY(240deg) translateZ(300px); 
} .content-carrousel figure:nth-child(8) {
	-webkit-transform: rotateY(280deg) translateZ(300px);
	        transform: rotateY(280deg) translateZ(300px); 
} .content-carrousel figure:nth-child(9) {
	-webkit-transform: rotateY(320deg) translateZ(300px);
	        transform: rotateY(320deg) translateZ(300px); 
} .content-carrousel figure:nth-child(10) {
	-webkit-transform: rotateY(360deg) translateZ(300px);
	        transform: rotateY(360deg) translateZ(300px); 
} 

.shadow {
    position: absolute;
    box-shadow: 0px 0px 20px 0px #000;
    border-radius: 1px;
}

.content-carrousel img {
	image-rendering: auto;
	transition: all 300ms;
	width: 100%;
	height: 100%;
}

.content-carrousel img:hover {
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	transition: all 300ms;
}

@-webkit-keyframes rotar {
	from {
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	} to {
		-webkit-transform: rotateY(360deg);
		        transform: rotateY(360deg);
	}
}

@keyframes rotar {
	from {
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	} to {
		-webkit-transform: rotateY(360deg);
		        transform: rotateY(360deg);
	}
}


/* Page content */

.info{
	margin-top: 20%;
	margin-left: 20%;
}

.info h2{
	color: orange;
}

.info sub{
	color: #8EFCBA;
	font-size: small;
}

.info p{
	color: #fff;
	font-size: x-large;
}

.started a{
	text-decoration: none;
	color: orange;
	background-color: #30c16a90;
	padding: 5px;
	font-weight: 800;
	border-radius: 6px;
}


@media (max-width: 950px){
	
	.info{
		margin-top: 70%;
		margin-left: 5%;
	}

	.info h2{
		color: orange;
		font-size: 25px;
	}

	.info sub{
		color: #8EFCBA;
		font-size: small;
	}

	.info p{
		color: #fff;
		font-size: 20px;
	}
}

/* Media Queries */

@media (min-width: 150px) and (max-width: 279px){

	.content-carrousel {
		display: none;
		width: 100%;
		position: absolute;
		float: right;
		-webkit-animation: rotar 15s infinite linear;
		        animation: rotar 15s infinite linear;
		-webkit-transform-style: preserve-3d;
		        transform-style: preserve-3d;
	}
	.content-carrousel figure {
		width: 40%;
		height: 50px;
		border: 1px solid #3b444b;
		overflow: hidden;
		position: absolute;
	}

	.info h2{
		font-size: 19px;
	}

	.info{
		margin-top: 5%;
		margin-left: 2%;
		margin-bottom: none;
	}
	
}


@media (min-width: 280px) and (max-width: 600px){
	.info h2{
		font-size: 19px;
	}
	

	.content-carrousel {
		width: 100%;
		position: absolute;
		float: right;
		-webkit-animation: rotar 15s infinite linear;
		        animation: rotar 15s infinite linear;
		-webkit-transform-style: preserve-3d;
		        transform-style: preserve-3d;
	}
	

	.content-carrousel figure {
		width: 60%;
		height: 110px;
		border: 1px solid #3b444b;
		overflow: hidden;
		position: absolute;
	}

}
